<template>
  <div class="comment-board">
    <div class="user">
      <div>
        <span class=" iconfont icon-gerenzhongxin"></span>
        <span class="text">{{ user.nickname }}, 您好</span>
      </div>
      <button class="btn" v-show="currentCommentId" @click="cancelHandle">取消回复</button>
    </div>
    <div class="board">
      <div class="avatar">
        <img :src="user.avatarUrl" :title="user.nickname" />
      </div>
      <div class="mess">
        <textarea v-model="commentContent" placeholder="别愣着，写啊！"></textarea>
      </div>
    </div>
    <div class="sub">
      <button class="btn" @click="commentSubmit">评论</button>
    </div>
  </div>
</template>

<script>
import { mapMutations, mapGetters } from 'vuex'

export default {
  data() {
    return {
 
      commentContent: ''
    }
  },
  computed: {
    ...mapGetters(['loginState', 'userInfo'])
  },
  props: ['currentCommentId'],
  created() {
    this.user = JSON.parse(sessionStorage.getItem('userInfo')) || this.userInfo
  },
  methods: {
    // 取消评论
    cancelHandle() {
      this.$emit('cancelHandle')
    },

    // 评论提交
    commentSubmit() {
      if (this.commentContent.trim().length === 0) {
        this.$message.error('喂喂，写点东西啊')
        return
      } else {
        this.$emit('commentSubmit', this.commentContent)
      }
    }
  }
}
</script>

<style scoped lang="less">
.user {
  margin-top: 30px;
  display: flex;
  justify-content: space-between;
  .btn {
    padding: 7px;
    font-size: 12px;
    color: #d9dfff;
    border: 1px #d9dfff solid;
    background-color: #fff;
    border-radius: 5px;
    outline: none;
  }
  .text {
    font-size: 14px;
    margin-left: 10px;
  }
}
.board {
  display: flex;
  margin-top: 20px;

  .avatar {
    img {
      width: 50px;
      height: 50px;
      border-radius: 50%;
    }
  }
  .mess {
    flex: 1;
    margin-left: 30px;
    textarea {
      width: 100%;
      outline: none;
      background: #f8f9ff;
      resize: none;
      padding: 6px 12px;
      color: #666;
      border: 1px solid #eaebff;
      border-radius: 3px;
      font-size: 13px;
      height: 80px;
      overflow: hidden;
    }
  }
}
.sub {
  height: 30px;
  margin-top: 15px;
  .btn {
    color: #fff;
    background: #ff416c;
    border-radius: 3px;
    border: none;
    padding: 10px 30px;
    outline: none;
    float: right;
  }
}
</style>
