import { axios } from './axios'
// 获得视频地址
export function videoUrl(id) {
  return axios({
    url: '/video/url',
    params: {
      id
    }
  })
}
// 获得详情
export function videoDetail(id) {
  return axios({
    url: '/video/detail',
    params: {
      id
    }
  })
}
// 获得相关视频
export function videoGroup(id) {
  return axios({
    url: '/video/group',
    params: {
      id
    }
  })
}
// 视频资源
export function videoRecommed(vid) {
  return axios({
    url: '/video/detail/info',
    params: {
      vid
    }
  })
}

// 评论
export function videoComment(data) {
  return axios({
    url: '/comment/video',
    params: {
      id: data.id,
      limit: data.limit,
      offset: data.offset
    }
  })
}

// 发送删除评论

export function sendComment(data) {
  return axios({
    url: '/comment',
    params: {
      t: data.t,
      type: data.type,
      id: data.id,
      content: data.content,
      commentId: data.commentId
    }
  })
}
// 点赞
export function commentLike(id, cid, t, type) {
  return axios({
    url: `/comment/like`,
    params: {
      id,
      cid,
      t,
      type
    }
  })
}
// 相关视频
export function relatedVideo(id) {
  return axios({
    url: '/related/allvideo',
    params: {
      id
    }
  })
}

// 获得视频标签
export function videoTagList() {
  return axios({
    url: '/video/group/list'
  })
}
// 获得视频分类列表
export function videoCategory() {
  return axios({
    url: '/video/category/list'
  })
}
// 获得视频标签分类下的视频
export function getTagVideo(id, offset) {
  return axios({
    url: '/video/group',
    params: {
      id,
      offset
    }
  })
}
// 获得全部视频
export function geAllVideo(offset) {
  return axios({
    url: 'video/timeline/all',
    params: {
      offset
    }
  })
}
