<template>
  <div class=" comments-list">
    <h2 class="title">{{ title }}</h2>
    <div class="content" v-for="item in commentsList" :key="item.commentId">
      <div class="user">
        <img :src="item.user.avatarUrl" :title="item.user.nickname" />
      </div>
      <div class="info">
        <div class="top">
          <div class="user-info">
            <span>{{ item.user.nickname }}</span>
            <span class="time">{{ utils.formTime(item.time) }}</span>
          </div>
          <div class="resources">
            <p class=" iconfont icon-zanpress count" @click="praise(item.commentId, item.liked)" :class="item.liked ? active : ''">
              <span> {{ item.likedCount }}</span>
            </p>
            <i>|</i>
            <span class=" iconfont icon-xinxi" @click="commentHandle(item.commentId)"></span>
          </div>
        </div>
        <div class="container">
          <div class="comments">{{ item.content }}</div>
          <div class="be-replied" v-for="item1 in item.beReplied" :key="item1.beRepliedCommentId">
            <span>@ {{ item1.user.nickname }} </span> : {{ item1.content }}
          </div>
        </div>
        <comment-board v-if="currentCommentId == item.commentId" :currentCommentId="currentCommentId" @commentSubmit="commentSubmit" @cancelHandle="cancelHandle"></comment-board>
      </div>
    </div>
  </div>
</template>

<script>
import commentBoard from '@/components/common/commentBoard/commentBoard'
export default {
  components: {
    commentBoard
  },
  props: ['commentsList', 'title', 'currentCommentId'],

  methods: {
    // 评论
    commentHandle(id) {
      this.$emit('commentHandle', id)
    },
    cancelHandle() {
      this.$emit('cancelHandle')
    },
    commentSubmit(data) {
      this.$emit('commentSubmit', data)
    },
    // 点赞
    praise(id,like) {
      this.$emit('praise',id,like)
    }
  }
}
</script>

<style scoped lang="less">
.comments-list {
  margin-top: 30px;
  .title {
    font-size: 20px;
  }
  .content {
    display: flex;
    margin: 20px 0;
    .user {
      img {
        width: 50px;
        height: 50px;
        border-radius: 50%;
      }
    }
    .info {
      margin-left: 15px;
      width: 100%;

      .top {
        display: flex;
        justify-content: space-between;
        .user-info {
          font-weight: 700;
          font-size: 15px;
          .time {
            font-weight: 400;

            margin-left: 10px;
            color: #a5a5c1;
            font-size: 12px;
          }
        }
        .resources {
          display: flex;
          .active{
            color: red;
          }
          .count {
            margin-right: 5px;
            span {
              font-size: 10px;
            }
          }
          i {
            margin: 0 7px;
            color: #999;
          }
        }
      }
      .container {
        margin-top: 15px;
        padding: 10px;
        background-color: #f5f5f5;
        font-size: 13px;
        border-radius: 8px;
        .be-replied {
          margin-top: 10px;
          background-color: #fff;
          padding: 10px;
          font-size: 13px;
          border-radius: 8px;
          span {
            color: red;
          }
        }
      }
    }
  }
}
</style>
