<template>
  <div class="limit">
    <div class="detail ">
      <div class="left">
        <div class="video ">
          <video :src="url" autoplay controls="controls" controlslist="nodownload"></video>
        </div>
        <h3 class="title">{{ detail.title }}</h3>
        <div class="tag">
          <span class="item" v-for="item in detail.videoGroup" :key="item.id">{{ item.name }}</span>
        </div>
        <div class="date">
          <span>发布 ：{{ detail.publishTime | dateFormat('YYYY-MM-DD') }}</span>
          <span>播放次数 : {{ detail.playTime }}</span>
        </div>
        <div class="resource">
          <span class=" iconfont icon-zan"> {{ resource.likedCount }}</span>
          <span class="iconfont icon-shoucang"> {{ detail.subscribeCount }}</span>
          <span class="iconfont icon-fenxiang"> {{ resource.shareCount }}</span>
        </div>
        <div class="comments">
          <div class="title">
            <span class="iconfont icon-zu"></span>
            <span class="text"> Comments</span> |
            <span class="text"> {{ resource.commentCount }} 评论</span>
          </div>
          <comment-board v-if="!currentCommentId" @commentSubmit="commentSubmit"></comment-board>
          <comment-list
            @commentHandle="commentHandle"
            @cancelHandle="cancelHandle"
            @commentSubmit="commentSubmit"
            @praise="praise"
            :currentCommentId="currentCommentId"
            :title="'精彩评论'"
            :commentsList="hotComments"
            v-if="hotComments.length > 0"
          ></comment-list>
          <comment-list
            @commentHandle="commentHandle"
            @cancelHandle="cancelHandle"
            @commentSubmit="commentSubmit"
            @praise="praise"
            :currentCommentId="currentCommentId"
            :title="'最新评论'"
            :commentsList="comments"
            v-if="comments.length > 0"
          ></comment-list>
          <el-pagination @current-change="handlePage" class="page" hide-on-single-page :current-page="currentPage" layout="prev, pager, next" :total="total"> </el-pagination>
        </div>
      </div>
      <div class="right">
        <div class="introduce" v-if="creator">
          <div class="title">视频简介</div>
          <div class="user-info">
            <img :src="creator.avatarUrl" alt="" />
            <span>{{ creator.nickname }}</span>
          </div>
          <div v-if="detail.description">{{ detail.description }}</div>
          <div v-else>视频暂无简介</div>
        </div>
        <div class="related">
          <div class="title">相关视频</div>
          <div id="video">
            <div class="item" v-for="item in relatedVideo" :key="item.vid" @click="pushDetail(item.vid)">
              <div class="header">
                <img class="img" :src="item.coverUrl" alt="" />
                <i class="icon el-icon-video-camera">
                  <span>{{ item.playTime }}</span></i
                >
                <div class="time">
                  <span>{{ item.durationms | dateFormat('MM:SS') }}</span>
                </div>
                <i class="contentIcon el-icon-video-camera"></i>
              </div>
              <div class="box">
                <div class="titlename ellipsis">{{ item.title }}</div>
                <div class="creator" v-for="item1 in item.creator" :key="item1.userId">{{ item1.userName }}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import commentBoard from '@/components/common/commentBoard/commentBoard'
import commentList from '@/components/common/commentList/commentList'
import { videoUrl, videoDetail, videoGroup, videoRecommed, videoComment, sendComment, commentLike, relatedVideo } from '@/network/video'

export default {
  components: {
    commentBoard,
    commentList
  },
  data() {
    return {
      id: '',
      url: '',
      detail: [],
      creator: [],
      resource: {},
      comments: [],
      total: 0,
      currentPage: 0,
      limit: 20,
      hotComments: [],
      currentCommentId: '',
      relatedVideo: []
    }
  },

  created() {
    if (this.$route.query.id) {
      this.id = this.$route.query.id
    }
    videoUrl(this.id).then(res => {
      this.url = res.data.urls[0].url
    })
    // 获得视频详情
    videoDetail(this.id).then(res => {
      this.id = res.data.data.vid
      this.detail = res.data.data
      this.creator = res.data.data.creator
    })
    // 获得资源
    videoRecommed(this.id).then(res => {
      this.resource = res.data
    })
    this.getVideoComment()
    // 相关视频
    relatedVideo(this.id).then(res => {
      this.relatedVideo = res.data.data
      console.log(this.relatedVideo)
    })
  },
  methods: {
    // 点击评论
    commentHandle(id) {
      this.currentCommentId = id
    },
    // 取消评论
    cancelHandle() {
      this.currentCommentId = ''
    },
    commentSubmit(content) {
      let timestamp = new Date().getTime()
      let params = {
        type: 5,
        id: this.id,
        content: content
        // timestamp
      }
      if (this.currentCommentId == '') {
        params.t = 1
      } else {
        params.t = 2
        params.commentId = this.currentCommentId
      }
      sendComment(params).then(res => {
        console.log(res)
      })
      this.$message.error('301状态码')
    },
    // 改变页码
    handlePage(index) {
      this.currentPage = index
      console.log(this.currentPage)
      this.offset = (index - 1) * this.limit
      this.getVideoComment()
    },
    // 获得评论
    getVideoComment() {
      let params = {
        id: this.id,
        limit: this.limit,
        offset: this.offset
      }
      videoComment(params).then(res => {
        this.total = res.data.total
        this.comments = res.data.comments || []
        this.hotComments = res.data.hotComments || []
      })
    },
    // 点赞
    praise(id, like) {
      let t = 0
      if (like) {
        t = 0
      } else {
        t = 1
      }
      commentLike(this.id, id, t, 5).then(res => {
        console.log(res)
      })
      this.$message.error('301状态码')
    },
    // 相关视频点击跳转
    pushDetail(id) {
      this.$router.push({
        path: '/videoDetail',
        query: {
          id
        }
      })
      window.location.reload()
    }
  }
}
</script>

<style scoped lang="less">
.detail {
  margin-top: 20px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  .left {
    width: 70%;
    background-color: #fff;
    border-radius: 10px;
    padding: 10px;
    .video {
      video {
        width: 100%;
        height: 100%;
        border-radius: 5px;
      }
    }
    .title {
      margin-top: 10px;
      font-weight: 500;
    }
    .tag {
      margin-top: 10px;
      font-size: 14px;
      color: red;
      .item {
        margin-right: 10px;
        cursor: pointer;
      }
    }
    .date {
      margin-top: 10px;
      font-size: 12px;
      color: #999;
      span {
        margin-right: 20px;
      }
    }
    .resource {
      margin-top: 15px;
      span {
        padding: 5px 10px;
        background-color: #ddd;
        border-radius: 15px;
        margin-right: 14px;
        font-size: 14px;
      }
    }
    .comments {
      margin-top: 30px;
      .title {
        line-height: 35px;
        border-bottom: 1px solid #f1f1f1;
        .text {
          font-size: 14px;
        }
        span {
          margin-right: 10px;
          font-size: 16px;
        }
      }
      .page {
        text-align: center;
      }
    }
  }
  .right {
    width: 28%;
    .introduce {
      width: 100%;
      background-color: #fff;
      border-radius: 10px;
      padding: 15px;
      margin-bottom: 50px;
      font-size: 14px;
      .title {
        font-size: 15px;
        padding: 5px 15px;
        font-weight: 600;
        border-left: 3px solid pink;
      }
      .user-info {
        display: flex;
        align-items: center;
        padding: 10px;
        margin-bottom: 7px;
        img {
          width: 50px;
          height: 50px;
          border-radius: 50%;
        }
        span {
          margin-left: 15px;
          font-size: 14px;
        }
      }
    }
    .related {
      width: 100%;
      background-color: #fff;
      border-radius: 10px;
      padding: 15px;
      margin-bottom: 50px;
      font-size: 14px;
      .title {
        font-size: 15px;
        padding: 5px 15px;
        font-weight: 600;
        border-left: 3px solid pink;
      }
      #video {
        width: 100%;
        height: 100%;
        margin-top: 20px;
        .item {
          height: 100%;
          width: 100%;
          padding: 0 10px 20px;
          .header:hover {
            .contentIcon {
              display: block;
            }
          }
          .header {
            position: relative;
            color: #fff;
            font-size: 14px;
            .img {
              width: 100%;
            }
            .icon {
              height: 20px;
              width: 25%;
              line-height: 20px;
              position: absolute;
              top: 0;
              right: 0;
              background: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.4));
              font-size: 14px;
              span {
                font-size: 12px;
                float: right;
                margin-right: 8px;
              }
            }
            .time {
              height: 20px;
              width: 100%;
              line-height: 20px;
              position: absolute;
              bottom: 4px;
              right: 0;
              background: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.4));

              span {
                float: right;
                margin-right: 8px;
              }
            }
            .contentIcon {
              position: absolute;
              top: 50%;
              left: 50%;
              font-size: 30px;
              color: #ec0801;
              transform: translate(-50%, -50%);
              display: none;
            }
          }
          .box {
            .titlename {
              margin-top: 5px;
              font-size: 14px;
              font-weight: 700;
            }
            .creator {
              font-size: 10px;
              margin-top: 10px;
              font-weight: 600;
            }
          }
        }
      }
    }
  }
}
</style>
